@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.movie {
    margin: 10px auto;
    width: 275px;
    color: #fff;
    box-shadow: 3px 3px 8px 1px rgba(0,0,0,0.44);
    position: relative;
    overflow: hidden;
}

.movie__image {
    width: 100%;
    height: 425px;
    object-fit: cover;
    margin-bottom: -4px;
}

.movie__info {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
    width: 100%;
    height: 405px;
    -webkit-transform: translateX(101%);
            transform: translateX(101%);
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
    top: 0;
    background-color: #1481ba;
    color: #fff;
    overflow: auto;
}

.movie__info p, a {
    padding: 10px;
    text-align: center;
}

.movie__info a {
    position: absolute;
    bottom: 20px;
    width: 70%;
    height: 40px;
    background-color: #25313d;
    color: #fff;
    border: 1px #000 solid;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    box-shadow: 3px 3px 8px 1px rgba(0,0,0,0.44);
}

.movie__info a:active {
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}

.movie__info a img {
    width: 50px;
    height: auto;
    margin-left: 5px;
}

/* .movie__image .movie__info {
    transform: translateX(0);
} */

.movie__infoReveal {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.movie__details {
    background-color: #034748;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}

.movie__details h4 {
    font-weight: 500;
    max-width: 220px;
}

.movie__infoIcon {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px #fff solid;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    cursor: pointer;
}
.movies__container {
    display: grid;
    grid-template-columns: repeat(auto-fit, 300px);
    justify-content: center;
}
.search__container {
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: left;
    margin-left: 20px;
}

.search {
    height: 50px;
    position: relative;
}

.search__input {
    background-color: #034748;
    color: #fff;
    border: 0;
    border-radius: 50px;
    font-size: 14px;
    height: 58px;
    width: 50px;
    transition: width 0.3s ease;
    padding-left: 10px;
    box-shadow: 3px 3px 8px 1px rgba(0,0,0,0.44);
}

.search__button {
    background-color: #034748;
    color: #fff;
    border: 0;
    border-radius: 50px;
    cursor: pointer;
    font-size: 24px;
    position: absolute;
    left: 0;
    height: 58px;
    width: 50px;
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    box-shadow: 8px 3px 8px 1px rgba(0,0,0,0.44);
}

.search__button, .search__input:focus {
    outline: none;
}

.search.active .search__input {
    width: 200px;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

.search.active .search__button {
    -webkit-transform: translateX(198px);
            transform: translateX(198px);
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}
* {
  box-sizing: content-box;
  margin: 0;
  font-family: 'Ubuntu', sans-serif;
}

body {
  background-color: #25313d;
}

.App {
  display: flex;
  justify-content: center;
}

.container {
  width: 1200px;
}

